<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
    inject: ['reload'],
    name: "devegroup",
    data () {
        return {
            columns: [
                {
                    title: 'ID',
                    key: 'id',
                    width: 80, 
                    align: 'center'
                },
                {
                    title: '名称',
                    key: 'name',
                    width: 320, 
                    align: 'center'
                },
                {
                    title: '简称',
                    key: 'sname',
                    width: 200, 
                    align: 'center'
                },
                {
                    title: '官网地址',
                    key: 'website',
                    width: 160, 
                    align: 'center'
                },
                {
                    title: '添加时间',
                    key: 'addtime',
                    width: 160, 
                    align: 'center'
                },
                {
                    title: '性质',
                    key: 'status',
                    width: '', 
                    align: 'center',
                    render: (h, params) => {
                        // console.log(params.row)
                        if(params.row) {
                            const status = params.row.status;
                            const text = status == 1 ? '普通' : status == 2 ? '热门' : status == 3 ? '推荐' : status == 4 ? '停止' : '-';
                        return h('div', {}, text)
                        }
                    }
                },
                {
                    title: '绑定游戏',
                    key: 'count_game',
                    width: 160, 
                    className: 'table-info-column',
                    align: 'center',
                    render: (h,params) => {
                        const count_game = params.row.count_game;
                        const gameurl = params.row.sname;
                        return h('a', {
                            style: {
                                color: '#515a6e'
                            },
                            on: {
                                click: () => {
                                    this.$router.push({
                                        path: '/data/gamelist/plat/' + gameurl
                                    })
                                }
                            }
                        }, count_game)
                    }
                },
                {
                    title: '绑定应用',
                    key: 'count_app',
                    width: 160, 
                    className: 'table-info-column',
                    align: 'center',
                    render: (h,params) => {
                        const count_app = params.row.count_app;
                        const appurl = params.row.sname;
                        return h('a', {
                            style: {
                                color: '#515a6e'
                            },
                            on: {
                                click: () => {
                                    this.$router.replace({
                                        path: '/data/applist/plat/' + appurl
                                    })
                                }
                            }
                        }, count_app)
                    }
                },
                {
                    title: '操作',
                    key: 'action',
                    width: 200, 
                    align: 'center',
                    render: (h,params) => {
                        return h('div', [
                            h('Button',{
                                domProps: {
                                    innerHTML: "编辑"
                                },
                                props: {
                                    type: 'primary',
                                    size: 'small',
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        this.gotomodelEdit(params)
                                    }
                                }
                            }, 'View'),
                            h('Button',{
                                domProps:{
                                    innerHTML: "删除"
                                },
                                props: {
                                    type: 'error',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        this.curId = params.row.id;
                                        // console.log(this.data,this.curId,this.curType)
                                        this.warnModal = true

                                        
                                    }
                                }
                            }, 'Delete')
                        ])
                    }
                },
            ],
            data: [],
            formData: {
            },
            isLoading: true,
            page: 1,
            per_page: 10,
            totalPages: 0,
            params: {},
            warnModal: false,
            modal_loading: false,
            curId: '',
            tagList:[],
            selectedGameId:'',
            plattypeModel:'0',
            platName: '',
            plat_type: ''
        }
    },
    mounted () {
        const token = getToken(),
            name = this.formData.name,
            status = this.formData.status*1,
            params = {"token": token, "page": "1", "per_page": 10,"name":name, "status":status};

            this.params = params;
            this.getData(params);
    },
    methods: {
        getData(params) {
            api.platList(params).then(res => {
                if(res.code === 0) {
                    this.data = res.data.platlist
                    this.plat_type = res.data.config.plat_type
                    this.totalPages = res.data.totlerows
                    this.isLoading = false 

                    this.data.forEach(ele => {
                        //格式化时间
                        ele.addtime = this.$moment(ele.addtime*1000).format("YYYY-MM-DD k:mm");
                    })
                } else {
                    this.$Notice.error({title: res.msg})
                }
            })
        },
        gotomodelEdit (params) {
        const id = params.row.id
        const type = params.row.type
        this.$router.push({
            path: '/data/exeplat',
            query: {"id": id,"type": type}
        })
        },
        changePage: function(page) {
        // console.log(page)
        this.params.page = page;
        this.getData(this.params);
        },
        changePageNum: function(num) {
        // console.log(num)
        this.params.per_page = num;
        this.getData(this.params);
        },
        delplat: function() {
        this.modal_loading = true

        const token = getToken(),
                id = this.curId,
                params = {"token": token, "id": id, "type": "plat"};

        api.delplat(params).then(res => {
            // console.log(res)
            this.warnModal = false
            this.modal_loading = false
            if(res.code == 0) {
            this.$Notice.success({
                "desc": res.msg
            })
            this.reload();
            } else {
            this.$Notice.error({
                "desc": res.msg
            })
            }
        }).catch(err => {
            this.warnModal = false
            this.modal_loading = false
            this.$Notice.error({
            "desc": err
            })
        })
        },
        natureData: function() {
            this.params.name = this.platName;
            this.params.status = this.plattypeModel
            this.getData(this.params)
        },
    },
    

}
</script>

<template>
    <div>
        <h3><Icon type="md-barcode" size=24 />研发商列表</h3>
        
        <div class="formBox">
            <div class="searchBar">
                <Button type="primary" to="/data/exeplat">添加研发商</Button>
            </div>

            <Select v-model="plattypeModel" style="width:100px;margin-left:20px;" @on-change="natureData">
                <Option value="0">性质</Option>
                <Option v-for="itm,idx in plat_type" :value="idx" :key="idx">{{ itm }}</Option>
            </Select>

            <div class="idbox"><span>名称：</span><Input v-model="platName" clearable style="width: 200px" /></div>

            <Button type="primary" style="width:100px" @click="natureData()">
                <Icon type="ios-search" size=16></Icon>
                搜索
            </Button>
        </div>

        <Table 
            border 
            ref="selection" 
            :columns="columns" 
            :data="data" 
            :loading=isLoading
            
            >
        </Table>

        <Modal v-model="warnModal" width="400" :styles="{top:'40%'}">
            <p slot="header" style="color:#f60;text-align:center">
                <Icon type="ios-information-circle" size=20></Icon>
                <span>删除确认</span>
            </p>
            <div style="text-align:center">
                <p>此操作会删除id为{{ curId }}的研发商。</p>
                <p>确认删除吗？</p>
            </div>
            <div slot="footer">
                <Button type="error" size="large" long :loading="modal_loading" @click="delplat">Delete</Button>
            </div>
        </Modal>

        <div style="margin: 10px;overflow: hidden">
            <div style="float: right;">
                <Page :total="totalPages*1" show-total show-elevator show-sizer @on-page-size-change="changePageNum" @on-change="changePage" />
            </div>
        </div>
    </div>
</template>

<style lang="scss" scoped>
    .formBox {
        height: 50px;
        margin-bottom: 20px;
        .searchBar{
            display: contents;
            padding-right: 20px;
        }
        .idbox {
            display:contents;
            span{
                margin-left:20px; 
            }
        }
        Button{
            margin-left: 20px;
        }
    }
    .ivu-table td.table-info-column{
        background-color: #2db7f5;
        color: #fff;
    }
</style>